body {
  background-color: cornflowerblue;
}
.main {
  background-color: cornflowerblue;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  align-content: center;
  vertical-align: top;
  row-gap: 10px;
  width: 100%;
  font-size: calc(10px + 2vmin);
  color: white;
}

.key:hover {
  position: absolute;
  stroke: black;
  fill: #f4d570;
  z-index: 2 !important;
}

.key:not(:hover) {
  position: absolute;

  z-index: 1 !important;
}

svg {
  align-self: center;
  pointer-events: none;
  min-width: 540px;
  transform: scale(2);
}

path {
  pointer-events: auto;
}

button {
  max-width: 310px;
  margin: auto;
  font-size: 24px;
  font-family: sans-serif;
}
